	onLoad(options) {
		this.id = options.id
		this.dataList.forEach((item)=>{
			if(item.id == options.id){
				this.detailData = item;
			}
		})
		this.detailData.singleList.forEach((item,index) => {
			let time     = item.remainingTime;
			var hours    = this._toDouble(parseInt(time/3600000));								//计算小时
			var minutes  = this._toDouble(parseInt((time-hours*3600000)/60000)); 				//计算分
			var seconds  = this._toDouble(parseInt((time-hours*3600000-minutes*60000)/1000));	//计算秒
			item.hours   = hours;
			item.minutes = minutes;
			item.seconds = seconds;
		})
		this.totalCount = this._getTotalNum();
		
		let historyArr = uni.getStorageSync('historyArr') || [];
		let index = this._isHasOne(this.id,historyArr)
		if(index == -1){
			historyArr.unshift({
				id: this.id,
				img: this.detailData.imgSrc[0],
				name: this.detailData.title,
				price: this.detailData.smallPrice
			})
		}else{
			historyArr.splice(index,1)
			historyArr.unshift({
				id: this.id,
				img: this.detailData.imgSrc[0],
				name: this.detailData.title,
				price: this.detailData.smallPrice
			})
		}
		uni.setStorageSync('historyArr',historyArr)
	},
	onShow(){
		let collectArr = uni.getStorageSync('collectArr') || [];
		this.isCollection = collectArr.some((item) => item.id == this.id)
	},
	methods: {
		// 轮播图动画改变时
		onChange(ev){
			this.currentIndex = ev.detail.current;
		},
		// 点击返回icon
		onBack(){
			uni.navigateBack({
				animationDuration: 100
			})
		},
		// 点击购物车icon
		onCart(){
			uni.switchTab({
				url: '/pages/cart/cart'
			})
		},
		// 点击预览轮播图片
		onPreviewBannerImage(index){
			uni.previewImage({
				urls: this.detailData.imgSrc,
				current: index
			})
		},
		// 点击预览评价详情图片
		onPreviewReviewDetailImage(index){
			uni.previewImage({
				urls: this.detailData.reviewDetail[index].reviewImg,
				current: index
			})
		},
		// 点击预览商品详情图片
		onPreviewGoodsDetailImage(index){
			uni.previewImage({
				urls: this.detailData.detailList,
				current: index
			})
		},
		// 点击预览详情图片
		onPreviewDetailImage(index){
			uni.previewImage({
				urls: this.detailData.detailList,
				current: index
			})
		},
		// 点击优惠券跳转优惠劵界面
		onOffer(){
			uni.navigateTo({
				url: '/pages/coupon/coupon'
			})
		},
		// 点击七天无理由退款
		onReturn(){
			this.isShowPopupReturn = true;
		},
		// 退货关闭按钮
		onCloseReturn(){
			this.isShowPopupReturn = false;
		},
		// 点击查看更多评价 跳转详情页面
		onReview(index){
			// 判断传过来的变量是否是一个数字
			if(typeof(index)=="number"){
				this.tagCurrentIndex = index;
			}
			uni.navigateTo({
				url: '/pages/review/review?index='+index+'&id='+this.id
			})
		},
		// 跳转首页icon
		goToHome(){
			uni.switchTab({
				url: '/pages/home/home'
			})
		},
		// 点击收藏icon
		onCollection(){
			let collectArr = uni.getStorageSync('collectArr') || [];
			let index = this._isHasOne(this.id,collectArr)
			if(this.isCollection){
				collectArr.splice(index,1)
			}else{
				collectArr.push({
					id: this.id,
					img: this.detailData.imgSrc[0],
					name: this.detailData.title,
					price: this.detailData.smallPrice
				})
			}
			uni.setStorageSync('collectArr',collectArr)
			this.isCollection = !this.isCollection;
		},
		// 点击客服icon
		onCustomer(){
			console.log('客服');
		},
		// 点击去拼单  弹出遮罩层
		onSingle(type){
			this.isShowPopup = true;
			this.btnType = type;
			this.goodsSize  = this.detailData.specificationList[0].specification[0].name;
			this.goodsColor = this.detailData.specificationList[1].specification[0].name;
		},
		// 发起拼单
		onStartFight(type){
			this.isShowPopup = true;
			this.btnType = type;
			this.goodsSize  = this.detailData.specificationList[0].specification[0].name;
			this.goodsColor = this.detailData.specificationList[1].specification[0].name;
		},
		// 单独购买
		onAloneShop(type){
			this.isShowPopup = true;
			this.btnType = type;
			this.goodsSize  = this.detailData.specificationList[0].specification[0].name;
			this.goodsColor = this.detailData.specificationList[1].specification[0].name;
		},
		// 点击关闭
		onClose(){
			this.isShowPopup = false;
		},
		// 点击样式选择，尺码
		onSpecification1(index2){
			this.specificationCurrentIndex0 = index2
			this.goodsSize = this.detailData.specificationList[0].specification[index2].name
		},
		// 点击样式选择,颜色
		onSpecification2(index2){
			this.specificationCurrentIndex1 = index2;
			this.goodsColor = this.detailData.specificationList[1].specification[index2].name
		},
		// 点击确定将选择的产品参数存起来跳转到订单页面
		onSure(){
			// 1发起拼单，2单独购买 3去拼单
			if(this.btnType == 1 || 3){
				let arr = []
				arr.push({
					id: this.detailData.id,
					title: this.detailData.title,
					price: this.detailData.AlonePrice,
					img: this.detailData.imgSrc[0],
					size: this.goodsSize,
					color: this.goodsColor,
					count:this.countText
				});
				uni.navigateTo({
					url: "/pages/confirmorder/confirmorder?arr="+JSON.stringify(arr)
				})
			}else if(this.btnType == 2){
				let obj = {
					id: this.detailData.id,
					title: this.detailData.title,
					price: this.detailData.AlonePrice,
					img: this.detailData.imgSrc[0],
					size: this.goodsSize,
					color: this.goodsColor
				}
				this._addToCart(obj,this.countText);
				uni.showToast({
				    title: '加入购物车成功',
				    duration: 2000,
					icon: "none"
				});
			}else if(this.btnType == 3){
				let arr = []
				arr.push({
					id: this.detailData.id,
					title: this.detailData.title,
					price: this.detailData.AlonePrice,
					img: this.detailData.imgSrc[0],
					size: this.goodsSize,
					color: this.goodsColor,
					count:this.countText
				});
				uni.navigateTo({
					url: "/pages/confirmorder/confirmorder?arr="+JSON.stringify(arr)
				})
			}
		},
		// 加按钮
		onAdd(){
			this.countText++
		},
		// 减按钮
		onCut(){
			this.countText--
			if(this.countText < 1){
				this.countText = 1
			}
		},
		//1.往购物车里面添加商品
		_addToCart(item,count){
		    let cartData = this._getDataFromStorage();
		    let index = this._isHasOne(item.id,cartData);
		    if(index == -1){
		        item.count = count;
		        item.status = true;
		        cartData.push(item);
		    }else{
		        //如果不是-1说明当前商品在购物车里面存在:只需要更改数量，将原来的加上新传过来的
		        cartData[index].count += count;
		    }
		    wx.setStorageSync('cart', cartData);
		},
		//2.拿购物车的数据:用参数来区分是拿所有商品还是只拿选中商品
		_getDataFromStorage(flag){	
		    let cartData = wx.getStorageSync('cart') || [];
		    let newArr = [];
		    if(flag){
		        for(let i=0;i<cartData.length;i++){
		            if(cartData[i].status){
		                newArr.push(cartData[i])
		            }
		        }
		        return newArr;
		    }else{
		        return cartData;
		    }
		},
		//3.查找在购物车中是否存在当前添加的商品
		_isHasOne(id,arr){
		    // 假设没有该商品，该数组中没有这个id，下标为-1
		    let index = -1;
		    for(let i=0;i<arr.length;i++){
		        if(id == arr[i].id){
		            index = i;
		            break;
		        }
		    }
		    return index;
		},
		//4.求所有的商品总数
		_getTotalNum(){
		    let cartData = this._getDataFromStorage();
		    let total = 0;
		    cartData.forEach(function(item){
		        total += item.count
		    })
		    return total;
		},
		// 时间 + 0
		_toDouble (num) {
			if(num<10){
				return '0'+num
			}else{
				return ''+num
			}
		}	
	}
}
